import React from 'react';
import PropTypes from "prop-types";
import Slider from "./Silder"


class VerticalCarousel extends React.Component{
    state = {
        index: 0

    };
    
    static propTypes = {
        slides: PropTypes.arrayOf(
            PropTypes.shape({
                key: PropTypes.any,
                gameName: PropTypes.string,
                content: PropTypes.string,
                icon: PropTypes.string,
                link: PropTypes.string
            })
        ).isRequired,
    };

    getPresentableSlides() {
        const { slides } = this.props;
        const presentableSlides = slides;
    
        return presentableSlides;
      }


    render(){
        return(
            <React.Fragment>
                {this.getPresentableSlides().map((slider, index) => (
                    <Slider key={slider.key} gameName={slider.gameName}
                        content={slider.content} icon={slider.icon} link={slider.link}></Slider>
                ))}
            </React.Fragment>
        )
    }
}

export default VerticalCarousel;